@import "./const.scss";


.task-collapsed,
.task-not-displayed {
  display: none;
}


.task-row {

  .drag-handler {
    width: $drag-width;
    transform: translate(0, 50%);

    .circle-column span.circle {
      background-color: $border-color;
    }
  }

}


.task-row {
  display: flex;
  padding: 0;
  margin: 0;
  height: $task-row-height;
  background: white;
  user-select: none;

  .task-title-column {
    display: flex;
    box-sizing: border-box;
  }

  .title {
    height: 100%;
    width: 100%;

    .long-click-input {
      height: 100%;
      width: 100%;

      button, input {
        height: 100%;
        width: 100%;
        background: $title-background;
        padding-left: $title-padding-left;
      }
    }

    // border-left: $cell-border-size solid $border-color;
  }

  .points {
    height: 100%;
    display: flex;

    input {
      width: 25px;
      height: 100%;
      background: $title-background;
      font-size: 10px;
      text-align: right;
      padding-right: 5px;
      color: $border-color;

      &.zero {
        color: transparent;

        &:hover,
        &:focus {
          color: black;
        }
      }
    }
  }


  .measures {
    height: 100%;
    display: flex;
  }
}

